@media screen and (min-width: 601px) {
  #HeaderFood h1 {
    font-size: 6vw;
  }
  #HeaderFood h2 {
    font-size: 2vw;
    color: grey;
  }
}
@media screen and (max-width: 600px) {
  #HeaderFood h1 {
    font-size: 2.5em;
  }
  #HeaderFood h2 {
    font-size: 2em;
    color: grey;
  }
}
#HeaderFood {
  width: 100%;
  margin-top: 10px;
}
#HeaderFood img {
  object-fit: cover;
  width: 12vw;
  height: 12vw;
  margin-left: -8vw;
  border-radius: 50%;
}
.handleHolder {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.profileHolder {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}
