.ComModal img{
  width: 400px;
  height: 400px;
  object-fit: cover;
}
.ComModal{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.modal-content{
  flex-direction: row !important;
}
.ComModalBody{
  width: 80% !important;
}
.modal-dialog {
}
@media (min-width: 576px){
  .modal-dialog {
    max-width: 900px !important;
  }
}
@media (max-width: 801px){
  .ComModal{
    justify-content: center;
  }
  .modal-dialog {
    max-width: 400px !important;
  }
}
.commentsHead{
  margin-top: 20px;
  text-align: center;
  width: 50%;
}
.commentsBody{
  text-align: start;
  background-color: rgb(255, 255, 255);
  height: 210px;
  overflow-x: scroll;
}
