.commentContent{
  margin: 20px;
  display: flex;
  align-items: center;
  background-color: rgb(235, 235, 235);
  padding: 10px;
  border-radius: 10px;
}
.commentContent img{
  width: 50px !important;
  height: 50px !important;
  border-radius: 50%;
}
.commentContent span{
  margin-left: 10px;
}
::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 7px;
}

::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(0, 0, 0, .5);
  box-shadow: 0 0 1px rgba(255, 255, 255, .5);
}